function sliderServices() {
    var swiper = new Swiper('.services .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            1024: {
                spaceBetween: 30,
            },
        }
    })
}

function sliderInfo2() {
    var swiper = new Swiper('.info2 .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        loop: true,
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            1024: {
                spaceBetween: 30,
            },
        },
        navigation: {
            nextEl: ".info2 .swiper-button-next",
            prevEl: ".info2 .swiper-button-prev",
        },
    })
}

function sliderCases() {
    var swiper = new Swiper('.cases .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            1024: {
                spaceBetween: 30,
            },
        },
        navigation: {
            nextEl: ".cases .swiper-button-next",
            prevEl: ".cases .swiper-button-prev",
        },
    })
}

function sliderFeedback() {
    var swiper = new Swiper('.feedback__left .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        centeredSlides: false,
        loop: false,
        breakpoints: {
            320: {
                spaceBetween: 15,
                centeredSlides: true,
                loop: true,
            },
            1024: {
                spaceBetween: 30,
                centeredSlides: false,
                loop: false,
            },
        },
        navigation: {
            nextEl: ".feedback__left .swiper-button-next",
            prevEl: ".feedback__left .swiper-button-prev",
        },
        on: {
            init: function () {
                if (window.matchMedia("(min-width: 1024px)").matches) {
                    let slidesCount = this.slides.length
                    this.slideTo(slidesCount-1,0)
                }
            },
        },
    })
}

function sliderFeedback2() {
    var swiper = new Swiper('.feedback__right .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 30,
        centeredSlides: false,
        loop: false,
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 15,
                centeredSlides: true,
                loop: true,
            },
            1024: {
                slidesPerView: 1,
                spaceBetween: 30,
                centeredSlides: false,
                loop: false,
            },
        },
        navigation: {
            nextEl: ".feedback__right .swiper-button-next",
            prevEl: ".feedback__right .swiper-button-prev",
        },
    })
}

sliderServices();
sliderInfo2();
sliderCases();
sliderFeedback();
sliderFeedback2()


$(document).ready(function () {

//Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })


    //Quiz
    $('.quiz-next').click(function (e) {
        $('.quiz-prev').addClass('quiz-prev--active')

        let activeStep =$('.quiz-step--active').attr('data-step')

        if (activeStep === '1') {
            if($('.quiz-input:checked').length > 0) {
                $('.quiz-step1').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step1').find('.quiz-error').addClass('quiz-error--active')
                $('.quiz-prev').removeClass('quiz-prev--active')
                return
            }
        }

        if(activeStep === "2"){
            if($('.quiz-input2').val() !== '' ) {
                $('.quiz-step2').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step2').find('.quiz-error').addClass('quiz-error--active')
                return;
            }
        }

        if(activeStep === "3"){
            if($('.quiz-input3').val() !== '' ) {
                $('.quiz-step3').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step3').find('.quiz-error').addClass('quiz-error--active')
                return;
            }
        }

        if (activeStep === '4') {
            if($('.quiz-input4:checked').length > 0) {
                $('.quiz-step4').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step4').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }


        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.next('.quiz-step').addClass('quiz-step--active');
        if ( $('.quiz-step').last().hasClass('quiz-step--active') ) {
            $('.quiz-next').addClass('remove')
            $('.quiz-form__btn').addClass('quiz-form__btn--active')
        }

        if (activeStep === '1') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '25%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(25)
            $('.quiz-step').parents('.quiz').find('.number-active').html(2)
        }

        if (activeStep === '2') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '50%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(50)
            $('.quiz-step').parents('.quiz').find('.number-active').html(3)
        }

        if (activeStep === '3') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '75%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(75)
            $('.quiz-step').parents('.quiz').find('.number-active').html(4)
        }

        if (activeStep === '4') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '100%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(100)
            $('.quiz-step').parents('.quiz').find('.number-active').html(5)
        }

    })

    $('.quiz-prev').click(function () {
        $('.quiz-next').removeClass('remove')
        $('.quiz-form__btn').removeClass('quiz-form__btn--active')

        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.prev('.quiz-step').addClass('quiz-step--active');
        if ( $('.quiz-step').first().hasClass('quiz-step--active') ) {
            $(this).removeClass('quiz-prev--active')
        }

        let activeStep =$('.quiz-step--active').attr('data-step')

        if (activeStep === '1') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '0%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(0)
            $('.quiz-step').parents('.quiz').find('.number-active').html(1)
        }

        if (activeStep === '2') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '25%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(25)
            $('.quiz-step').parents('.quiz').find('.number-active').html(2)
        }

        if (activeStep === '3') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '50%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(50)
            $('.quiz-step').parents('.quiz').find('.number-active').html(3)
        }

        if (activeStep === '4') {
            $('.quiz-step').parents('.quiz').find('.progress-bar span').css('width', '75%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(75)
            $('.quiz-step').parents('.quiz').find('.number-active').html(4)
        }

    })



//Zoom
    function updateZoomVariable() {
        // Определяем ширину окна браузера
        const screenWidth = window.innerWidth;
        let zoomValue;

        if (screenWidth > 1920) {
            // Вычисляем zoom для экранов шире 1920px
            zoomValue = (screenWidth / 1920).toFixed(2);
        } else {
            // Для экранов 1920px или уже, устанавливаем zoom равным 1
            zoomValue = 1;
        }

        // Устанавливаем CSS переменную --zoom для корневого элемента (HTML)
        document.documentElement.style.setProperty('--zoom', zoomValue);
    }

// Вызываем функцию первоначально, чтобы установить переменную при загрузке страницы
    updateZoomVariable();

// Добавляем слушатель событий, чтобы обновлять переменную при изменении размера окна
    window.addEventListener('resize', updateZoomVariable);    

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}

})
  